.clientTypology{
    background-color: color(dark);
    padding-top: s(132);
    padding-bottom: s(140);
    &__title{
        @extend .subtitle;
        color: color(white);
        text-align: center;
        margin-bottom: s(50);
    }
    &__list{
        display: flex;
        flex-direction: column;

    }
    &__item{
        padding: s(35/2) 0;
        color: color(sand);
        text-align: center;
        @extend .title80;
        text-decoration: none;
        position: relative;
        span{
            z-index: 2;
            position: relative;
            &:before{
                background: color(sand);
                bottom: 1px;
                content: "";
                height: 3px;
                left: 0;
                position: absolute;
                transform: scaleX(0);
                transform-origin: right center;
                transition: transform .5s easing(ease-in-out-quint);
                width: 100%;
                will-change: transform;
    
            }
        }


        &:hover,
        &:focus{
            span:before{
                transform: scaleX(1);
                transform-origin: left center;
            }

            .clientTypology__image{
                opacity: 1;
                //transition: 200ms opacity linear;
                z-index: 1;
            }
        }
    }

    &__image{
        opacity: 0;
        position: fixed;
        left: calc(50vw + 62px) ;
        top: 50vh;
        width: s(545);
        height: s(545);
        border-radius: s(545);
        margin-top: s(-545/2);
        opacity: 0;
        pointer-events: none;
        //transition: 200ms opacity linear 200ms;

        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
            display: block;
            border-radius: s(545);
            opacity: 0.5;

        }
        //z-index: -1;
    }
}